import styled from 'styled-components'

export default styled.button`
  width: 2.5rem;
  height: 2.5rem;
  display: inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-left: 0.5rem;
  padding: 0;
  color: #fff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.25);
  transition: 250ms all ease;
`



